import { Fragment, useEffect } from "react";
import style from "./IssuedBooks.module.css";
import { useTranslation } from "react-i18next";
import { imageShowUrl } from "../../../../../../services/BaseUrl";
import moment from "moment";
import { useState } from "react";
import CollectModal from "./CollectModal/CollectModal";

function IssuedBookItem({
  status,
  book,
  issueId,
  member,
  onRefetchWhenBookCollect,
  isStaff,
}) {
  const { t } = useTranslation();
  const [openModal, setOpenModal] = useState(false);
  const [submittedDate, setSubmittedDate] = useState("");

  useEffect(() => {
    if (book?.createdAt && book?.for_days) {
      let dt = new Date(book?.createdAt);
      dt.setDate(dt.getDate() + +book?.for_days);
      setSubmittedDate(dt);
    }
  }, [book?.createdAt, book?.for_days]);
  return (
    <>
      {isStaff ? (
        <Fragment>
          <div className={style.issuedBookItemTop}>
            <div className={style.issuedBookItemTopleft}>
              <img
                className={style.bookItemImg}
                src={
                  book?.book?.photo
                    ? `${imageShowUrl}/${book?.book?.photo}`
                    : "/images/defaultBook.png"
                }
              />
            </div>
            <div className={style.issuedBookItemTopright}>
              <h6 className={style.title}>
                {book?.book?.bookName?.length >= 18
                  ? `${book?.book?.bookName?.match(/.{1,18}/g)[0]} ...`
                  : book?.book?.bookName}
              </h6>
              <p className={style.author}>
                {book?.book?.author} &nbsp;
                <span className={style.authorLabel}>{t("author")}</span>
              </p>
              <p className={style.author}>
                {t("accession_number")} {" : "}
                {book?.book?.accession_number ?? ""}{" "}
              </p>
              <p className={style.booklang}> {book?.book?.language}</p>
            </div>
          </div>

          <div className={style.issuedBookItemBottom}>
            <div className={style.issuedBookItemBottomLeft}>
              <h6
                style={{
                  fontSize: "13px",
                  fontWeight: "400",
                }}
              >
                {t("issue_date")}:{" "}
                {moment(book?.createdAt).format("Do MMM YYYY")}
              </h6>
            </div>
          </div>
        </Fragment>
      ) : (
        <div
          className={
            status === "memberdetail"
              ? `${style.issuedBookItem} ${style.fullwidth}`
              : style.issuedBookItem
          }
        >
          <div className={style.issuedBookItemTop}>
            <div className={style.issuedBookItemTopleft}>
              <img
                className={style.bookItemImg}
                src={
                  book?.book?.photo
                    ? `${imageShowUrl}/${book?.book?.photo}`
                    : "/images/defaultBook.png"
                }
              />
            </div>
            <div className={style.issuedBookItemTopright}>
              <h6 className={style.title}>
                {book?.book?.bookName?.length >= 18
                  ? `${book?.book?.bookName?.match(/.{1,18}/g)[0]} ...`
                  : book?.book?.bookName}
              </h6>
              <p className={style.author}>
                {book?.book?.author} &nbsp;
                <span className={style.authorLabel}>{t("author")}</span>
              </p>
              <p className={style.author}>
                {t("accession_number")} {" : "}
                {book?.book?.accession_number ?? ""}{" "}
              </p>
              <p className={style.booklang}> {book?.book?.language}</p>
            </div>
          </div>
          <div className={style.issuedBookItemMid}>
            <div className={style.issuedBookItemMidleft}>
              <div className={style.imgcontainer}>
                <img
                  src={
                    member?.studentProfilePhoto || member?.staffProfilePhoto
                      ? `${imageShowUrl}/${
                          member?.studentProfilePhoto ??
                          member?.staffProfilePhoto
                        }`
                      : "/images/member_tab/class/default_avatar.svg"
                  }
                  alt=""
                />
              </div>
            </div>
            <div className={style.issuedBookItemMidright}>
              {member?.studentFirstName ? (
                <>
                  <h6>
                    {" "}
                    {`${member?.studentFirstName ?? ""} ${
                      member?.studentMiddleName ? member?.studentMiddleName : ""
                    } ${member?.studentLastName ?? ""}`}
                  </h6>
                  <p>
                    {t("gr_no")} {member?.studentGRNO ?? ""}{" "}
                  </p>
                </>
              ) : (
                <>
                  <h6>
                    {" "}
                    {`${member?.staffFirstName ?? ""} ${
                      member?.staffMiddleName ? member?.staffMiddleName : ""
                    } ${member?.staffLastName ?? ""}`}
                  </h6>
                  <p>
                    {t("index")} {member?.staffROLLNO ?? ""}{" "}
                  </p>
                </>
              )}
            </div>
          </div>
          <div className={style.issuedBookItemBottom}>
            <div className={style.issuedBookItemBottomLeft}>
              <h6
                style={{
                  fontSize: "13px",
                  fontWeight: "400",
                }}
              >
                {t("issue_date")}:{" "}
                {moment(book?.createdAt).format("Do MMM YYYY")}
              </h6>
              {member?.studentFirstName ? (
                <h6
                  style={{
                    fontSize: "13px",
                    fontWeight: "400",
                  }}
                >
                  {t("to_be_submitted")}: {` `}
                  {submittedDate && moment(submittedDate).format("Do MMM YYYY")}
                </h6>
              ) : null}
            </div>
            {status !== "studentMemberDetail" && (
              <div
                className={style.collectbtn}
                onClick={() => setOpenModal(true)}
              >
                {t("collect")}
              </div>
            )}
          </div>

          {openModal && (
            <CollectModal
              open={openModal}
              hideModal={(w) => {
                setOpenModal(w);
              }}
              issueId={issueId}
              onRefetchWhenBookCollect={onRefetchWhenBookCollect}
              isStaff={member?.staffFirstName ? true : false}
              issueDate={book?.createdAt}
              dayCharge={book?.day_overdue_charge}
              forDays={book?.for_days ? +book?.for_days : 0}
            />
          )}
        </div>
      )}
    </>
  );
}

export default IssuedBookItem;
